@use "global" as *;

.blog {
    @include mq(md){
        position: relative;
    }
    // @include mq(md){
    //     background-image: url(../../assets/images/common/blog-bg.jpg);
    //     background-repeat: no-repeat;
    //     background-size: contain;
    //     background-position: top;
    // }
}

.blog__img {
    @include mq(md){
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 60%;
    }
}

.blog__img img {
    @include mq(md){
        object-fit: cover;
        width: 100%;
        height: 100%;
    }
}

.blog__inner {
    @include mq(md){
        position: relative;
        padding-top: rem(100);
        padding-bottom: rem(10);
    }
}

.blog__decoration {
    display: none;
    @include mq(md){
        display: block;
        position: absolute;
        top: rem(23);
        width: rem(437);
        left: max(50% - 49vw, -55px);
    }
}

.blog__items {
    margin-top: rem(29);
    padding-right: rem(7);
    padding-left: rem(7);
    @include mq(md){
        margin-top: rem(81);
        padding: 0;
    }
}

.blog__items a {
    transition: .3s;
}


.blog__items a:hover {
    scale: 1.02;
}

.blog__btn {
    margin-top: rem(40);
    text-align: center;
    padding-right: rem(5);
    @include mq(md){
        margin-top:rem(80);
        padding-right: rem(7);
    }
}
